/* Ad Network – lightweight embeddable script */
import { HttpAgent, Actor } from "@dfinity/agent";
import { idlFactory } from "./ad_network_idl.js";

// ===== Constants =====
const CANISTER_ID = "qanay-uyaaa-aaaag-qbbwa-cai";   // Ad Network canister
const HOST = location.hostname === "localhost" ? "http://localhost:4943" : "https://ic0.app";

// ===== Safe console + logger =====
const safeConsole = (() => {
  try { return (typeof window !== "undefined" && window.console) ? window.console : {}; }
  catch { return {}; }
})();
["log","info","warn","error","debug"].forEach(m => {
  if (typeof safeConsole[m] !== "function") { safeConsole[m] = function(){}; }
});

// ===== Utilities =====
function normalizeNextAdResult(result) {
  // Candid Opt<T> is [] for None, [value] for Some
  if (!Array.isArray(result) || result.length === 0) {
    return null;
  }

  const inner = result[0];

  // Tuple as array [ad, token]
  if (Array.isArray(inner) && inner.length === 2) {
    return inner;
  }

  // Fallback if tuple as object {0: ad, 1: token}
  if (typeof inner === 'object' && inner !== null && '0' in inner && '1' in inner) {
    return [inner[0], inner[1]];
  }

  return null;
}

const adTypeSize = {
  "Horizontal Banner Portrait": { w: 1090, h: 225 },
  "Vertical Banner Portrait":   { w: 225,  h: 1090 },
  "Full Page Portrait":         { w: 1080, h: 1920 },
  "Full Page Landscape":        { w: 1920, h: 1080 },
  "Horizontal Banner Landscape": { w: 1920, h: 400 },  // NEW: Added assumed size for "Horizontal Banner Landscape" (adjust if needed)
};

// ===== AdNetworkEmbed Class =====
class AdNetworkEmbed {
  constructor(scriptEl) {
    this.scriptEl = scriptEl;
    this.dataset = scriptEl.dataset || {};
    this.projectId = this.dataset.projectId || scriptEl.getAttribute('data-project-id') || 'Ad Network';
    this.adType = this.dataset.adType || scriptEl.getAttribute('data-ad-type') || 'Horizontal Banner Portrait';
    this.targetId = this.dataset.target || scriptEl.getAttribute('data-target') || null;

    this.agent = new HttpAgent({ host: HOST });
    this.actor = null;
    this.container = this.targetId ? document.getElementById(this.targetId) : document.createElement('div');
    if (!this.targetId && scriptEl.parentNode) {
      scriptEl.parentNode.insertBefore(this.container, scriptEl.nextSibling);
    }
    this.container.style.display = 'block';  // CHANGED: 'inline-block' to 'block' to remove baseline spacing/white line
    this.container.style.position = 'relative';
    this.container.style.overflow = 'hidden';
    this.container.style.lineHeight = '0';  // NEW: Eliminate any font-related vertical space

    const size = adTypeSize[this.adType] || { w: 728, h: 90 };
    // CHANGED: Added '|| this.adType.includes('Full Page Portrait')' to treat Full Page Portrait as responsive (fits screen width, maintains ratio)
    const isHorizontal = this.adType.includes('Horizontal Banner') || this.adType.includes('Landscape') || this.adType.includes('Full Page Portrait');
    if (isHorizontal) {
      this.container.style.width = '100%';  // Full width of parent/viewport
      this.container.style.height = '0';    // Height provided by padding
      this.container.style.paddingBottom = `${(size.h / size.w) * 100}%`;  // Maintain aspect ratio
    } else {
      this.container.style.width = `${size.w}px`;
      this.container.style.height = `${size.h}px`;
    }
    this.container.style.minWidth = isHorizontal ? '100%' : `${size.w}px`;   // Prevent collapse for horizontal
    this.container.style.minHeight = isHorizontal ? 'auto' : `${size.h}px`;

    this.img = null;
    this.viewTimer = null;
    this.blobUrl = null;  // For revocation

    // Event listeners
    document.addEventListener('visibilitychange', this.handleVisibilityChange);
    window.addEventListener('pagehide', this.clearViewTimer);
    window.addEventListener('beforeunload', this.clearViewTimer);
  }

  createActor = async () => {
    try {
      if (this.actor) return this.actor;
      const agent = this.agent;
      this.actor = Actor.createActor(idlFactory, { agent, canisterId: CANISTER_ID });
      return this.actor;
    } catch (err) {
      this.debug('createActor error:', err);
      throw err;
    }
  };

  debug = (...args) => {
    safeConsole.debug('[AdNetworkEmbed]', ...args);
  };

  hide = () => {
    this.container.style.display = 'none';
  };

  showFallback = (msg) => {
    const fallbackDiv = document.createElement('div');
    fallbackDiv.style.textAlign = 'center';
    fallbackDiv.style.color = 'gray';
    fallbackDiv.style.padding = '10px';
    fallbackDiv.style.width = '100%';
    fallbackDiv.style.height = '100%';
    fallbackDiv.style.boxSizing = 'border-box';
    fallbackDiv.style.display = 'flex';
    fallbackDiv.style.alignItems = 'center';
    fallbackDiv.style.justifyContent = 'center';
    fallbackDiv.innerText = msg;
    this.container.innerHTML = '';
    this.container.appendChild(fallbackDiv);
  };

  clearViewTimer = () => {
    if (this.viewTimer) {
      clearTimeout(this.viewTimer);
      this.viewTimer = null;
      this.debug('View timer cleared');
    }
    if (this.blobUrl) {
      URL.revokeObjectURL(this.blobUrl);
      this.blobUrl = null;
    }
  };

  handleVisibilityChange = () => {
    if (document.visibilityState === 'hidden') {
      this.clearViewTimer();
    }
  };

  destroy = () => {
    document.removeEventListener('visibilitychange', this.handleVisibilityChange);
    window.removeEventListener('pagehide', this.clearViewTimer);
    window.removeEventListener('beforeunload', this.clearViewTimer);
    this.clearViewTimer();
    if (this.interval) clearInterval(this.interval);
  };

  fetchAndShow = async () => {
    this.debug('Fetching ad for project:', this.projectId, 'adType:', this.adType);
    try {
      const actor = await this.createActor();
      const result = await actor.getNextAd(this.projectId, this.adType);
      this.debug('Raw response:', result);
      const normalized = normalizeNextAdResult(result);
      this.debug('Normalized:', normalized);
      if (!normalized) {
        this.debug('No ad available');
        this.showFallback('No ad available');
        return;
      }
      const [ad, tokenId] = normalized;
      const b64 = ad.imageBase64 || '';
      if (!b64) {
        this.debug('No image data');
        this.showFallback('No ad image');
        return;
      }

      // NEW: Decode base64 to binary for Blob (better for large images)
      let byteArray;
      try {
        byteArray = Uint8Array.from(atob(b64), c => c.charCodeAt(0));
        this.debug('Base64 decoded; length:', byteArray.length);
      } catch (e) {
        this.debug('Base64 decode error:', e);
        this.showFallback('Invalid image data');
        return;
      }

      let mime = 'image/jpeg';
      if (b64.startsWith('iVBORw0KGgo')) mime = 'image/png';

      const blob = new Blob([byteArray], { type: mime });
      this.blobUrl = URL.createObjectURL(blob);

      this.img = document.createElement('img');
      this.img.src = this.blobUrl;
      this.img.alt = 'Ad';
      this.img.style.position = 'absolute';
      this.img.style.top = '0';
      this.img.style.left = '0';
      this.img.style.width = '100%';
      this.img.style.height = '100%';
      this.img.style.objectFit = 'cover';
      this.img.decoding = 'async';
      this.img.loading = 'eager';  // NEW: Change to 'eager' to load immediately
      this.img.onerror = (e) => {
        this.debug('Image load error:', e);
        this.showFallback('Image failed to load');
      };

      this.img.addEventListener('load', () => {
        this.debug('Image loaded; natural size:', this.img.naturalWidth, 'x', this.img.naturalHeight);
        if (this.img.naturalWidth === 0) {
          this.debug('Loaded but zero size - invalid image?');
          this.showFallback('Invalid image');
          return;
        }
        if (this.viewTimer) return;
        this.viewTimer = setTimeout(async () => {
          try {
            await actor.recordViewWithToken(tokenId);
            this.debug('View recorded');
          } catch (e) {
            this.debug('recordView error:', e);
          }
          this.viewTimer = null;
        }, 5000);
        this.debug('Timer started');
      }, { once: true });

      this.container.innerHTML = '';
      if (ad.clickUrl) {
        const a = document.createElement('a');
        a.href = ad.clickUrl;
        a.target = '_blank';
        a.rel = 'noopener noreferrer nofollow';
        a.style.position = 'absolute';
        a.style.top = '0';
        a.style.left = '0';
        a.style.width = '100%';
        a.style.height = '100%';
        a.style.display = 'block';
        a.appendChild(this.img);
        this.container.appendChild(a);
        this.container.style.cursor = 'pointer';
      } else {
        this.container.appendChild(this.img);
        this.container.style.cursor = 'default';
      }

      // NEW: Add "Ad" badge overlay
      const badge = document.createElement('div');
      badge.innerText = 'Ad';
      badge.style.position = 'absolute';
      badge.style.top = '0';
      badge.style.right = '0';
      badge.style.padding = '2px 6px';
      badge.style.backgroundColor = 'rgba(0, 0, 0, 0.6)';
      badge.style.color = 'white';
      badge.style.fontSize = '12px';
      badge.style.fontFamily = 'Arial, sans-serif';
      badge.style.borderRadius = '0 0 0 4px';
      badge.style.zIndex = '1';  // Ensure above image
      this.container.appendChild(badge);

      this.debug(`Rendered Ad #${ad.id} (${ad.adType}); token: ${tokenId}`);
    } catch (err) {
      this.debug('fetchAndShow error:', err);
      this.showFallback('Error loading ad');
    }
  };

  start = () => {
    this.fetchAndShow();
    // CHANGED: Removed 'this.interval = setInterval(this.fetchAndShow, 60000);' to prevent automatic cycling
  };

  // NEW: Manual refresh method (call via window.AdNetworkEmbed.instances[0].refresh() or similar from host page)
  refresh = () => {
    this.fetchAndShow();
  };
}

// ===== Main =====
function initAllEmbeds() {
  const scripts = document.querySelectorAll('script[src*="ad-network-embed"]');
  const instances = [];
  scripts.forEach(scriptEl => {
    const instance = new AdNetworkEmbed(scriptEl);
    instance.start();
    instances.push(instance);
  });

  window.AdNetworkEmbed = window.AdNetworkEmbed || {};
  window.AdNetworkEmbed.instances = instances;  // NEW: Expose instances for external access
  window.AdNetworkEmbed.refresh = (index = 0) => {
    if (instances[index]) instances[index].refresh();
  };

  window.addEventListener('unload', () => instances.forEach(inst => inst.destroy()));
}

if (document.readyState === "loading") {
  document.addEventListener("DOMContentLoaded", initAllEmbeds, { once: true });
} else {
  initAllEmbeds();
}