// Ad_Network_rollup/ad_network_idl.js
export const idlFactory = ({ IDL }) => {
  const Ad = IDL.Record({
    id: IDL.Nat,
    name: IDL.Text,
    imageBase64: IDL.Text,
    clickUrl: IDL.Text,
    adType: IDL.Text,
    viewsPurchased: IDL.Nat,
    viewsServed: IDL.Nat,
  });

  return IDL.Service({
    getNextAd: IDL.Func([IDL.Text, IDL.Text], [IDL.Opt(IDL.Tuple(Ad, IDL.Nat))], []),
    recordViewWithToken: IDL.Func([IDL.Nat], [IDL.Bool], []),
  });
};